import React from "react";
import {
  addPostActionCreater,
} from "../../../redux/profille-reducer";
import MyPosts from "./MyPosts";
import { connect } from "react-redux";
import { reset } from "redux-form";


 // функция добовляющая пост
let mapStateToProps = (state) => {
  return {
    posts: state.profilePage.posts,
    newPostText: state.profilePage.newPostText
  }
};
// функция добовляющая пост


// функция отслеживает изменения в теги TEXTAREA, отслеживает кажлдый новый символ
let mapDispatchToProps = (dispatch) => {
  return {
    addPostsContainer: (newPostText) => {
      dispatch(addPostActionCreater(newPostText)); // диспатчим action в stat
      dispatch(reset('profileAddMessage'))
    },
  }
};
// конец функции

const MyPostsContainer = connect(mapStateToProps, mapDispatchToProps)(MyPosts);

export default MyPostsContainer;
