import React from "react";
import { Field, reduxForm } from "redux-form";
import { maxLengthCreator, required } from "../../../utils/validators/validators";
import { Textarea } from "../../common/FormsControls/FormsControls";
import s from "./MyPosts.module.css";
import Post from "./Post/Post";

const MyPosts = React.memo(
  (props) => {
    console.log('render MyPost')
    let postsElement = [...props.posts].reverse().map((p) => {
      /* через метод MAP мвы в параметре M передает каждый обьект posts по очереди 
      и помещаем в компоненту POST как PROPS */
      return <Post message={p.message} likesCount={p.lieksCount} />;
    });
  
    let addMessage = (formData) => {
      console.log(props)
      props.addPostsContainer(formData.newPostText)
    }
  
    return (
      <div className={s.postsBlock}>
        <h3>my post</h3>
        <div>
          <OnFormMyPostRedux onSubmit={addMessage}/>
        </div>
        <div className={s.posts}>
          {postsElement}
          {/* компоненты Post мы зарефакатарели через метод обьекта MAP и превратили 
          все посты в строчку вышел
          <Post message={postsData[0].message} likesCount={postsData[0].lieksCount} />
          <Post message={postsData[1].message} likesCount={postsData[1].lieksCount} /> */}
        </div>
      </div>
    );
  }
)

const maxLength10 = maxLengthCreator(10);

let OnFormMyPost = (props) => {
  console.log(props)
  return (
    <form onSubmit={props.handleSubmit} action="">
      <div>
        <Field 
          name="newPostText" 
          placeholder="Your Message"
          component={Textarea}
          validate={[required, maxLength10]} />
      </div>
      <div>
        <button>Add post</button>
      </div>
    </form>
  );
};

let OnFormMyPostRedux = reduxForm({form: "profileAddMessage"})(OnFormMyPost)

export default MyPosts;
