import React from "react";
import Profile from "./Profile";
import { connect } from "react-redux";
import { 
  getUserProfileTC,
  getStatusTC,
  updateStatusTC,
  savePhotoTC,
} from "../../redux/profille-reducer";
import { useParams } from "react-router-dom";
import { withAuthRedirect } from "../../hoc/withAuthRedirect";
import { compose } from "redux";

export function withRouter(Children){
  return(props)=>{
     const match  = {params: useParams()};
     return <Children {...props}  match = {match}/>
 }
}

class ProfileContainer extends React.Component {

  refreshProfile() {
    let userId = this.props.match.params.userId;
    if(!userId) {
      userId = this.props.autorizedUserId;
    }
    this.props.getUserProfileTC(userId);
    console.log(this.props)
    this.props.getStatusTC(userId);
  } 

  componentDidMount() {
    this.refreshProfile();
  }
  
  componentDidUpdate(prevProps) {
    if(this.props.match.params.userId != prevProps.match.params.userId) {
      this.refreshProfile();
    }
  }

  render() {
    return (
      <div>
        <Profile
          isOwner= {!this.props.match.params.userId}
          {...this.props}
          profile={this.props.profile}
          status={this.props.status}
          updateStatusTC={this.props.updateStatusTC}
          savePhotoTC={this.props.savePhotoTC}
        />
      </div>
    );
  }
}


let maxStateToProps = (state) => ({
  profile: state.profilePage.profile,
  status: state.profilePage.status,
  updateStatusTC: state.profilePage.updateStatusTC,
  autorizedUserId: state.auth.id,
  isAuth: state.auth.isAuth,
  savePhotoTC: state.profilePage.savePhotoTC,
})

/* const WithRouterComponent = (props) => {
  debugger
  const params = useParams();
  return (
      <ProfileContainer
          {...props} // Пропсы из mapStateToProps, {setUserProfile}
          userId={params.userId ? params.userId : '2'} // Если такого userId нету, то отобразить 2
      />
  );
} */

export default compose(
  connect(maxStateToProps, {getUserProfileTC, getStatusTC, updateStatusTC, savePhotoTC}),
  withRouter,
  withAuthRedirect,
)(ProfileContainer);
